/* home css */
.home-container {
  background-image: url('https://assets.ccbp.in/frontend/react-js/home-lg-bg.png');
  min-height: 100vh;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.responsive-home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 90%;
  max-width: 1100px;
  margin: auto;
}

@media (max-width: 576px) {
  .home-container {
    background-image: url('https://assets.ccbp.in/frontend/react-js/home-sm-bg.png');
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: auto;
  }
}
.home-heading {
  font-family: 'Roboto';
  color: #f1f5f9;
  font-size: 46px;
}
.home-description {
  font-family: 'Roboto';
  color: #f8fafc;
  font-size: 20px;
  width: 90%;
  max-width: 800px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.find-jobs-btn {
  border-width: 0;
  border-radius: 6px;
  margin-top: 30px;
  font-weight: 500;
  height: 45px;
  width: 140px;
  background-color: #4f46e5;
  color: #f1f5f9;
  font-size: 17px;
  cursor: pointer;
}

@media (max-width: 576px) {
  .responsive-home-container {
    justify-content: flex-start;
    margin-bottom: auto;
  }
  .home-heading {
    font-size: 26px;
  }
  .home-description {
    font-size: 17px;
  }
  .find-jobs-btn {
    width: 100px;
  }
}
