/* jobitem css */
.job-item-container {
  background-color: #272727;
  width: 100%;
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
  padding: 25px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 12px;
}
.company-logo {
  width: 60px;
}
.job-logo-container {
  display: flex;
  align-items: center;
}
.job-role-rating-container {
  display: flex;
  flex-direction: column;
  margin-left: 15px;
}
.job-title {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 18px;
  margin-bottom: 0;
  margin-top: 16px;
}
.rating-container {
  display: flex;
  align-items: center;
  margin-top: 0;
}
.job-rating {
  color: #ffffff;
  font-family: 'Roboto';
  font-size: 18px;
  margin-left: 7px;
  /* margin-top: 3px; */
}
.star-icon {
  color: #fbbf24;
  font-size: 18px;
  margin-top: 0;
}
.location-package-details {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.location-container {
  display: flex;
  align-items: center;
  margin-right: 18px;
}
.location-icon {
  color: #f8fafc;
  font-size: 22px;
  margin-right: 8px;
}
.location {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 13px;
  font-weight: 400;
}
.location-employment-container {
  display: flex;
  align-items: center;
}
.package {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 16px;
  font-weight: 500;
}
.horizontal-line {
  width: 100%;
  border-width: 1px;
  background-color: #64748b;
  border-color: #64748b;
  border-style: solid;
}
.description {
  color: #dbe0e6;
  font-family: 'Roboto';
  font-size: 14px;
  line-height: 1.8;
}
.description-text {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 17px;
  margin-bottom: 0;
}

@media (max-width: 390px) {
  .location {
    font-size: 12px;
  }
  .location-icon {
    font-size: 18px;
    margin-right: 5px;
  }
  .package {
    font-size: 14px;
  }
}
