/* login css */
.login-form-bg-container {
  background-color: #000000;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-form {
  background-color: #272727;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  max-width: 400px;
  padding: 30px;
  padding-top: 40px;
  padding-bottom: 40px;
}
.website-logo {
  width: 150px;
  margin-bottom: 30px;
}
@media (max-width: 576px) {
  .website-logo {
    width: 100px;
  }
}
.input-container {
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  width: 90%;
  margin: auto;
  margin-top: 20px;
}
@media (max-width: 576px) {
  .login-form {
    padding: 20px;
    padding-bottom: 30px;
    padding-top: 30px;
  }
}
.input-feild {
  background-color: transparent;
  border-color: #64748b;
  border-width: 1px;
  border-style: solid;
  border-radius: 3px;
  height: 38px;
  padding-left: 13px;
  outline: none;
  color: #f1f5f9;
  font-weight: 300;
  font-family: 'Roboto';
}
.label-el {
  color: #f1f5f9;
  font-family: 'Roboto';
  font-size: 12px;
  margin-bottom: 5px;
  font-weight: 400;
}
.login-button {
  border-width: 0;
  border-radius: 6px;
  margin-top: 30px;
  font-weight: 500;
  height: 38px;
  width: 90%;
  background-color: #4f46e5;
  color: #f1f5f9;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 5px;
}
.error-msg {
  color: #ff0b37;
  font-size: 13px;
  font-family: 'Roboto';
  margin-top: 10px;
  align-self: flex-start;
  width: 90%;
  margin: auto;
}
