.job-item-bg-container {
  background-color: #000000;
  min-height: 90vh;
  background-size: cover;
  padding-bottom: 30px;
  padding-top: 15vh;
}
.job-item-responsive-container {
  background-color: #272727;
  width: 90%;
  max-width: 1140px;
  display: flex;
  flex-direction: column;
  border-radius: 12px;
  margin: auto;
  padding: 25px;
  padding-left: 35px;
  padding-right: 35px;
}
.similar-jobs-container {
  width: 90%;
  max-width: 1140px;
  display: flex;
  flex-direction: column;
  border-radius: 12px;
  margin: auto;
}
.job-details {
  display: flex;
  align-items: center;
}

.company-logo {
  width: 60px;
}
.description-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.job-logo-container {
  display: flex;
  align-items: center;
}
.job-role-rating-container {
  display: flex;
  flex-direction: column;
  margin-left: 15px;
}
.job-title {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 18px;
  margin-bottom: 0;
  margin-top: 16px;
}
.rating-container {
  display: flex;
  align-items: center;
  margin-top: 0;
}
.job-rating {
  color: #ffffff;
  font-family: 'Roboto';
  font-size: 18px;
  margin-left: 7px;
  /* margin-top: 3px; */
}
.star-icon {
  color: #fbbf24;
  font-size: 18px;
  margin-top: 0;
}
.location-package-details {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.location-container {
  display: flex;
  align-items: center;
  margin-right: 18px;
}
.location-icon {
  color: #f8fafc;
  font-size: 22px;
  margin-right: 8px;
}
.location {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 13px;
  font-weight: 400;
}
.location-employment-container {
  display: flex;
  align-items: center;
}
.package {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 16px;
  font-weight: 500;
}
.horizontal-line {
  width: 100%;
  border-width: 1px;
  background-color: #64748b;
  border-color: #64748b;
  border-style: solid;
}
.job-description {
  color: #dbe0e6;
  font-family: 'Roboto';
  font-size: 17px;
  line-height: 1.9;
}
.job-description-text {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 22px;
  margin-bottom: 0;
}

@media (max-width: 490px) {
  .location {
    font-size: 12px;
  }
  .location-icon {
    font-size: 18px;
    margin-right: 5px;
  }
  .job-description-text {
    font-size: 16px;
  }
  .package {
    font-size: 14px;
  }
  .job-description {
    font-size: 14px;
    line-height: 1.5;
  }
}
.visit-logo {
  font-size: 20px;
  color: #4f46e5;
}
.visit {
  color: #4f46e5;
  font-family: 'Roboto';
  font-size: 17px;
  margin-right: 8px;
}
a {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin-top: 8px;
}
.skills-list {
  display: flex;
  list-style: none;
  padding-left: 0;
  flex-wrap: wrap;
}
.skill-item {
  width: 30%;
  margin-bottom: 38px;
  display: flex;
  align-items: center;
}

.skill-img {
  width: 50px;
  margin-right: 12px;
}
.skill-text {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 16px;
}
.skills-heading {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 20px;
}
@media (max-width: 576px) {
  .skill-item {
    width: 50%;
  }
  .skill-img {
    width: 35px;
    margin-bottom: 15px;
  }
  .skill-text {
    font-size: 14px;
  }
  .skills-heading {
    font-size: 16px;
    margin-top: 10px;
  }
}
.life-at-company {
  display: flex;
  /* align-items: center; */
  justify-content: space-between;
}
.life-at-company-text {
  width: 60%;
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 17px;
  font-weight: 400;
  line-height: 1.5;
}
.life-at-company-img {
  width: 30%;
  margin-left: 20px;
}
.life-at-company-heading {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 20px;
  margin-top: 0;
}

@media (max-width: 600px) {
  .life-at-company {
    flex-direction: column;
  }
  .life-at-company-text {
    width: 100%;
    font-size: 14px;
    font-weight: 300;
    margin-bottom: 20px;
  }
  .life-at-company-img {
    width: 100%;
    max-width: 400px;
    margin-left: 0;
  }
  .life-at-company-heading {
    font-size: 16px;
    margin-bottom: 1px;
  }
  .job-item-responsive-container {
    padding: 20px;
  }
}
.similar-jobs-heading {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 25px;
  margin-top: 30px;
}
.similar-jobs-lists {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  padding-left: 0;
}
.jobs-failure-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #000000;
  height: 100vh;
}
.jobs-faliure-img {
  width: 400px;
}
.jobs-failure-heading {
  color: #f8fafc;
  font-family: 'Roboto';
  font-size: 30px;
  margin-top: 20px;
  text-align: center;
}
.jobs-failure-text {
  color: #64748b;
  font-family: 'Roboto';
  font-size: 15px;
  margin-top: 10px;
}
.jobs-retry-btn {
  border-width: 0;
  border-radius: 6px;
  height: 35px;
  width: 90px;
  background-color: #4f46e5;
  color: #f1f5f9;
  font-size: 14px;
  cursor: pointer;
}
.loader-jobs-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  background-color: #000000;
}
